@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css');
@import url('https://fonts.googleapis.com/css2?family=Luxurious+Roman&display=swap');

/* *{
    background-color: hsl(217,58%,11%);
