body{
    display:flex;
    justify-content:center;
    align-items:center;
    height: 100vh;
    margin: 0;
    font-size: 20px;
    background-size: cover;
}   

.card-search{
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    color: #fff;
    padding: 5em;
    border-radius:50px;
    width: 100vw;
    max-width: 420px;
    margin: 1em ;
    opacity: 0.90;   
}

.container-search{
    display:flex;
    align-items: center;
    justify-content: center;
}

.searchBar{
    outline: none;
    background-color:#000;
    border: none;
    color: #fff;
    width: 100%;
    padding:20px;
    
}

button{
    border:none;
    outline: none;
    background: #333;
    color: #fff;
    padding:19.5px;
    cursor: pointer;
    transition: 0.2 ease-in-out;

}

button:hover{
    background-color: #0093E9;
    background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
    color: #000;
}
.fa-solid{
    font-size:15px;
}

::-webkit-input-placeholder{
    font-size:1em;
    color: #fff;
    text-transform: uppercase;
    font-family: 'fantasy';
    letter-spacing: 3px;
}

.weather{
    font-size: 1em;
    font-family: 'Roboto';
    font-style: italic;
    text-transform: uppercase;
    letter-spacing:5px;
    padding: 2em;
}

.positionflex{
    display:flex;
    align-items: center;
}

